<template>
  <div id="app">
    <v-header></v-header>

    <!-- 主体内容 -->
    <div class="content">
      <router-view :plans="plans" :plan="selectedPlan" @addPlan="addPlan" @editPlan="editPlan" @editSavePlan="editSavePlan" @cleanSelected="cleanSelected"></router-view>
    </div>
  </div>
</template>

<script>
import Header from 'components/header/Header'

import EventHub from '@/EventHub'

export default {
  name: 'app',
  data () {
    return {
      nickname: '',
      name: '',
      uid: '',
      plans: [],
      selectedPlan: null,
      selectedIndex: -1
    }
  },
  methods: {
    addPlan (plan) {
      this.plans.push(plan)
    },
    editPlan (selectedPlan, selectedIndex) {
      this.selectedPlan = selectedPlan
      this.selectedIndex = selectedIndex
    },
    editSavePlan (plan) {
      this.plans.splice(this.selectedIndex, 1, plan)

      this.cleanSelected()
    },
    cleanSelected () {
      this.selectedPlan = null
      this.selectedIndex = -1
    },
    setApp (app) {
      this.nickname = app.nick_name
      this.name = app.name
      this.uid = app.uid

      this.plans = app.projects.map(item => {
        let transducers = item.sensors.map(item => {
          return {
            id: item.id,
            type: item.type,
            name: this.typeMap[item.type],
            functions: item.functions,
            uid: item.uid
          }
        })

        let effectors = item.effectors.map(item => {
          return {
            id: item.id,
            type: item.type,
            name: this.typeMap[item.type],
            functions: item.functions,
            uid: item.uid
          }
        })

        return {
          name: item.project_name,
          type: item.authority ? 'public' : 'private',
          desc: item.description,
          createTime: item.create_time,
          lastEditTime: item.change_time,
          uid: item.uid,
          isrun: item.is_run,
          transducers,
          effectors
        }
      })
    }
  },
  created () {
    this.typeMap = {
      'buzzer-gpio': '蜂鸣器',
      'ky-016': 'LED灯',
      'ruff-v1-infrared-sender': '红外发射器',
      'lcd1602-pcf8574a-hd44780': '显示器',
      'button-gpio': '按钮',
      'sound-01': '声音传感器',
      'dht11': '温湿度传感器',
      'gy-30': '光照传感器'
    }

    EventHub.$on('set-app', this.setApp)
  },
  beforeDestroy () {
    EventHub.$off('set-app')
  },
  components: {
    'v-header': Header
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus" scoped>
</style>



